import { SingleModelResponse } from "./SingleModelResponse";
import { FileExportBlobTableViewModel } from "../ViewModel/FileExportBlobTableViewModel";

export class FileExportBlobTableResponse extends SingleModelResponse<
  FileExportBlobTableViewModel
> {
  Model: FileExportBlobTableViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileExportBlobTableViewModel();
      this.Model.exec_file_seq=obj.Model.exec_file_seq;
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.db_conn_id=obj.Model.db_conn_id;
      this.Model.parameter_column_group=obj.Model.parameter_column_group;
      this.Model.sql_statement=obj.Model.sql_statement;
      this.Model.is_active=obj.Model.is_active;
      this.Model.exec_seq=obj.Model.exec_seq;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;

      this.Model.is_active_desc = obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.tablefullname = obj.Model.tablefullname;
      this.Model.db_conn_string = obj.Model.db_conn_string;
      this.Model.tablefullname=obj.Model.tablefullname;
      this.Model.db_conn_string=obj.Model.db_conn_string;

      this.Model.src_export_type_key = obj.Model.src_export_type_key;
      this.Model.src_export_type = obj.Model.src_export_type;
      this.Model.is_exported_sql=obj.Model.is_expoted_sql;
      if(this.Model.is_exported_sql===true)
      {
        this.Model.exported_sql_color="danger";
      }
      else
      this.Model.exported_sql_color="light";

      this.Model.is_exported_header=obj.Model.is_exported_header;
      if(this.Model.is_exported_header===true)
      {
        this.Model.exported_header_color="danger";
      }
      else
       this.Model.exported_header_color="light";
    }
  }
}



